.folio-responsive__profile {
    width: 98%;
    position: relative;
    height: calc( 100vh - 160px );
    margin: 0 auto;
    max-width: 400px;
    margin-top: 9rem;
}

.folio-responsive__profile  img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    border-radius: 0px;
    max-height: 700px;
}

.folio-responsive__profile h2 {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(0, -80%);
    font-size: 4rem;
    -webkit-text-stroke: 2px var( --text-color );
    color: var( --text-color )
}

.folio-responsive__body {
    width: 95%;
    margin: 3rem auto;
}

.folio-responsive__body-section{
    width: 100%;
    height: 620px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.folio-responsive__body-section img {
    width: 100%;
    height: 78%;
    border-radius: 0px;
    max-width: 390px;
    margin: 0 auto;
}

.folio-responsive__body-section p {
    width: 80%;
    height: 22%;
    max-width: 350px;
    color: var( --text-color );
    margin-bottom: 2rem;
}

.folio-responsive__body-section h5{
    max-width: 350px;
    color: var( --text-color );
    margin-top: 2rem;
    width: 80%;
    margin-bottom: 1.3rem;
}

.folio-responsive__body-images {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}

.folio-responsive__body-images img {
    height: 500px;
    width: 100%;
    margin: 1rem 0;
    border-radius: 0px;
    max-width: 350px;
}

.folio-responsive__parallax {
    width: 100%;
    height: 100px;
    position: relative;
}

.folio-responsive__parallax-image {
    width: 50%;
    height: 50%;
    background: url(../../assets/folio/effect.jpg);
    background-attachment: fixed;
    background-position:center;
    background-size: cover;
    position: absolute;
    transform: translate(60%, -50%);
    top: 50%;
}

.folio-desktop__profile {
    width: 90%;
    position: relative;
    /* height: calc( 100vh - 160px ); */
    margin: 0 auto;
    /* max-width: 400px; */
    margin-top: 15rem;
    display: flex;
    justify-content: flex-end;
}

.folio-desktop__profile img {
    /* position: absolute; */
    left: 0;
    top: 0;
    width: 100%;
    height: 700px;
    display: flex;
    align-items: flex-end;
    border-radius: 0px;
    max-height: 700px;
    max-width: 550px;
}

.folio-desktop__profile h2 {
    position: absolute;
    top: 0;
    left: 35%;
    transform: translate(-50%, -50%);
    font-size: 4rem;
    /* -webkit-text-stroke: 2px var( --text-color ); */
    color: var( --text-color )
}

.folio-desktop__body-section {
    width: 100%;
    height: 520px;
    margin: 0 auto;
    display: flex;
    /* flex-direction: column; */
    justify-content: center;
    align-items: flex-start;
    margin-top: 5rem;
    position: relative;
}
.folio-desktop__body-section img {
    /* width: 100%; */
    height: 100%;
    border-radius: 0px;
    max-width: 390px;
    /* margin: 0 auto; */
}

.folio-desktop__body-section p {
    width: 100%;
    height: 22%;
    /* margin: 1rem 0; */
    max-width: 400px;
    /* margin: 0 auto; */
    margin-top: 5rem;
    padding-left: 3rem;
    color: var( --text-color )
}

.folio-desktop__body-section h5 {
    font-size: 2rem;
    /* width: max-content; */
    position: absolute;
    left: 60%;
    top: -5%;
    transform: translateX(-50%);
    max-width: 450px;
    width: 100%;
    -webkit-text-stroke: 1px var( --text-color );
}

.folio-desktop__body-images {
    /* display: flex; */
    width: 100%;
    /* flex-wrap: wrap; */
    /* justify-content: space-evenly; */
    /* align-items: center; */
    margin: 5rem 0;
    position: relative;
    height: 500px;
}

.folio-desktop__body-images img {
    height: 500px;
    width: 100%;
    margin: 1rem 0;
    border-radius: 0px;
    max-width: 350px;
    position: absolute;
    /* right: 0; */
    /* top: -200%; */
}

.folio-desktop__body-images .img-left {
    left: 1%;
    top: 0;
}
.folio-desktop__body-images .img-right {
    right: 0%;
    top: -70%;
}

.folio-desktop__body {
    width: 100%;
    margin: 3rem auto;
    min-width: 720px;
}

@media(min-width: 820px){
    .folio-desktop__body-images .img-left {
        left: 5%;
        top: 0;
    }
    .folio-desktop__body-images .img-right {
        right: 5%;
        top: -70%;
    }
}

@media(min-width: 950px){
    .folio-desktop__body-images .img-left {
        left: 10%;
        top: 0;
    }
}

.ReactModal__Overlay {
    z-index: 99999;
}

.modal-folio  {
    width: 100%;
    outline: none;
}

.modal-folio__content {
    position: relative;
    width: 100%;
    height: 100vh;
}

.modal-folio__content button {
    position: absolute;
    color: var( --text-color );
    font-weight: bold;
    top: 0;
    right: 0;
    border: none;
    outline: none;
    padding: 0 10px;
    opacity: 0.5;
    margin: 2rem;
    font-size: 1.3rem;
    /* border: 1px solid var( --text-color ); */
    transition: border 0.3s ease;
    background: transparent;
}

.modal-folio__content button:hover {
    opacity: 0.7;
}

.modal-folio__content img {
    width: 80%;
    height: 80%;
    max-width: 500px;
    max-height: 700px;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    object-fit: fill;
}

.parallax-border {
    height: 1px;
}