.nav-info {
    left: 0;
    top: 0;
    
}

.nav-contact {
    right: 0;
    top: 0;
}

.nav-message {
    bottom: 0;
    right: 0;
}

.nav-social {
    bottom: 0;
    left: 0;
}

.router-responsive {
    padding-top: .5rem;
}

.router-responsive__link {
    color: var( --text-color );
}

.router-responsive__link:hover {
    color: var( --text-color );
}

.nav-route:hover {
    color: rgba(0, 0, 0, 0.856) !important;
    text-decoration: none;
}

.nav-active {
    color: rgb(255, 41, 41) !important;
}

.nav-info a, .nav-contact a, .nav-social a {
    color: var( --text-color );
}

.nav-info, .nav-contact, .nav-social, .nav-message {
    margin: 1rem;
    display: flex;
    flex-direction: column;
    font-size: 1rem;
    z-index: 1;
}

.btn-nav__responsive {
    color: var( --text-color );
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    background: var( --color-light );
}

.btn-nav__responsive i {
    color: var( --text-color ) !important;
}

.nav-desktop {
    width: 100%;
    z-index: 1;
    position: fixed;
}

.offcanvas-end {
    top: 0;
    right: 0;
    max-width: 400px;
    border-left: 1px solid rgba(0,0,0,.2);
    transform: translateX(100%);
    width: 80%;
}

.offcanvas.show {
    transform: none;
    max-width: 400px;
    width: 80%;
}

.navbar-bg {
    background: #f2f2f2;
}

.navbar {
    padding-left: 0;
    padding-right: 0;
}