.contact-responsive{
    display: flex;
    width: 100%;
    height: 40px;
    align-items: center;
    justify-content: space-between;
    color: var( --text-color )
}

.contact-responsive p {
    margin: 0;
    transition: color 0.3s ease;
}

.contact-responsive a {
    color: var( --text-color );
    text-decoration: none;
    transition: color 0.3s ease;
}

.contact-responsive p:hover, .contact-responsive a:hover {
    color: rgba(0, 0, 0, 0.856) !important;
}