
.home-responsive {
    width: 95%;
    margin: 0 auto;
    padding-top: 1rem;
}

.home-responsive h1 {
    font-size: 4rem;
    font-weight: bold;
    margin-bottom: 1rem;
    padding: 0 .4rem;
    color: var( --text-color )
}
.view-responsive {
    text-decoration: none !important;
    color: var( --text-color );
    transition: color 0.3s ease;
}

.view-responsive:hover {
    color: rgb(255, 41, 41) !important;
}

.home-responsive__image {
    position: relative;
    width: 100%;
    height: 600px;
}

.home-responsive__image img {
    width: 100%;
    height: 100%;
    object-fit: fill;
}

.home-responsive__image a {
    position: absolute;
    top: 10px;
    left: 10px;
    font-size: 1.2rem;
    color: var( --text-color )
}

.home-responsive__contact {
    padding: 1rem .2rem;
    color: var( --text-color )
}

.home-desktop {
    position: relative;
    width: 90%;
    height: 500px;
    /* transform: translate(50%, 50%); */
    /* top: 50%; */
    /* left: 50%; */
    display: flex;
    justify-content: center;
    flex-direction: row;
    max-width: 900px;
    margin: auto;
    margin-top: 5rem;
}

.home-desktop img {
    width: 70%;
    height: 100%;
    max-width: 400px;
    object-fit: fill;
}

.home-desktop a {
    width: 30%;
    height: max-content;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin: auto 0;
    margin-left: 1rem;
    color: var( --text-color );
    text-decoration: none;
    transition: color 0.3s ease;
}

.home-desktop a:hover{
    color: rgb(255, 41, 41) !important;
}

.home-desktop h1 {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 25%;
    left: 65%;
    z-index: 2;
    font-size: 5rem;
    width: max-content;
    -webkit-text-stroke: 2px var( --text-color );
    color: var( --text-color )
    
}

.home-responsive h1 pre {
    font-size: 1rem;
    -webkit-text-stroke: 0px var( --text-color );
    color: var( --text-color );
}

.home-desktop h1 pre {
    font-size: 1.5rem;
    -webkit-text-stroke: 0px var( --text-color );
    color: var( --text-color );
}