.modal-contact  {
    z-index: 1500;
}

.modal-contact  {
    width: 100%;
    border-radius: 0 !important;
    padding: 0 !important;
    left: 0 !important;
    bottom: 0 !important;
    border: none !important;
    max-width: 520px;
    background: transparent !important;
}

.modal-message {
    background: var( --color-light );
    padding: 1rem 2rem;
    padding-top: 1.5rem;
    border-top: 1px solid var( --text-color );
}

.modal-message__desktop {
    background: var( --color-light );
    padding: 1rem 2rem;
    padding-top: .5rem;
    width: 500px;
    position: fixed;
    bottom: 0;
    right: 0;
    border: 1px solid var( --text-color );
}

.modal-button {
    width: 100%;
    height: 50px;
}

.modal-button button {
    display: flex;
    justify-self: center;
    align-items: center;
    margin: 0;
    font-size: 2rem;
    width: 30px;
    height: 30px;
    border: none;
    outline: none;
    background: var( --color-light );
    color: var( --text-color );
    margin-top: 1rem;
}

.modal-form button {
    border: none;
    outline: none;
    background: var( --text-color );
    color: var( --color-light );
    padding: .4rem 1rem;
    transition: background 1s ease-in;
    transition: color 1s ease;
    align-self: flex-start;
    margin: 2rem 0;
    margin-bottom: 0rem;
    width: 100%;
}

.modal-form button:hover {
    background: var( --color-light );
    color: var( --text-color );
}

.modal-info{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    max-width: 330px;
}

.modal-info__item {
    max-width: 150px;
}
.modal-info__item h5 {
    margin-bottom: 1rem;
}

.modal-form {
    width: 100%;
}

.modal-form input {
    width: 100%;
    margin-bottom: 1.3rem;
    
}

.modal-form input, .modal-form textarea{
    outline: none;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.726);
    background: transparent;
    transition: border-bottom 0.3s ease;
}

.modal-form textarea{
    width: 100%;
}

.modal-form input:hover, .modal-form textarea:hover{
    border-bottom: 1px solid var( --text-color );
}