.about-banner {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.about-image {
    width: 95%;
    /* margin: 0 auto; */
    margin-top: 6rem;
    position: relative;
    height: 400px;
    max-width: 320px;
    justify-self: flex-end;
}

.about-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.about-image h2 {
    position: absolute;
    color: var( --text-color );
    /* -webkit-text-stroke: 2px var( --text-color ); */
    font-size: 3rem;
    top: 0;
    left: 0;
    transform: translate(0%, -80%);
}

.about-content {
    justify-self: flex-start;
    max-width: 400px;
    width: 95%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* margin: 0 auto; */
    margin-top: 3rem;
}

.about-info p {
    text-align: left;
    font-size: 1rem;
}

.about-info button {
    border: none;
    outline: none;
    background: var( --text-color );
    color: var( --color-light );
    padding: .4rem 1rem;
    transition: background 1s ease-in;
    transition: color 1s ease;
    align-self: flex-start;
    margin: 2rem 0;
}

.about-info a {
    color: var( --color-light );
    transition: color .3s ease-in;
    text-decoration: none;
}


.about-info button:hover  a {
    color: var( --text-color ) !important;
}

.about-info button:hover {
    background: var( --color-light );
    color: var( --text-color );
}

.about-touch{
    max-width: 100%;
    margin: 0 auto;
}

.about-touch h3 {
    font-size: 2rem;
    color: var( --text-color );
    text-align: left;
    width: 100%;
}

.about-touch__contact {
    width: 100%;
    margin-top: 2rem;
    display: flex;
    flex-wrap: wrap;
}

.about-touch__contact-item {
    width: 100%;
    max-width: 300px;
}

.about-touch__contact-item p {
    margin-top: .7rem;
    margin-bottom: 2rem;
}

@media(min-width: 750px){

    .about-banner {
        margin-top: 8rem;
    }

    .about-info{
        margin-top: 12rem;
        padding-left: 3rem;
    }
    .about-image h2 {
        font-size: 4rem;
    }
    .about-touch h3 {
        font-size: 3.5rem;
        
    }
    .about-touch{
        margin-left: 5rem;
        width: 80%;
    }
}